footer{
    background: var(--color-primary-varient);
    padding: 1rem;
    text-align: center;
    font-size: 1rem;
    margin-top: 2rem;
}
footer a{
    color: var(--color-bg);
}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    /* margin-bottom: 2rem; */
    display: inline-block;
}
.permalink{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 1rem;
}
.footer__socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 1rem;
}

.footer__socials a{
    background: var(--color-bg);
color: var(--color-white);
padding: 0 .8rem;
border-radius: .7rem;
display: flex;
border: 1px solid transparent;
}

.footer__socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
    padding-bottom: 1.5rem;

}

.footer__copyright h5{
    display: flex;
    justify-content: center;
    
}